.footer-container {
    background-color: #123c69;
    padding: 4rem 0 2rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
}

.footer-subscription {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 24px;
    padding: 24px;
    color: #fff;
}

.footer-subscriptionheading {
    margin-bottom: 24px;
    font-size: 24px;
    float: center;
    color: #fff;
}

.footer-subscription-text {
    margin-bottom: 24px;
    font-size: 20px;
    color: #fff;
}

.descriptioncontact {
    font-size: 18px;
    text-align: justify;
    margin: auto;
    max-width: 600px;
    min-width: 750px;
    padding: 0.5rem 1rem;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    float: center;
}

.App-header {
    height: 3vmin;
    width: 3vmin;
    float: center;
    color: #fff;
}

.titlehome {
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
}

.footer-input {
    padding: 8px 20px;
    border-radius: 2px;
    margin-right: 10px;
    outline: none;
    border: none;
    font-size: 18px;
    margin-bottom: 16px;
    border: 1px solid #fff;
}

.footer-links {
    width: 100%;
    max-width: 1000px;
    display: flex;
    justify-content: center;
}

.footer-link-wrapper {
    display: flex;
}

.footer-link-items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px;
    text-align: left;
    width: 160px;
    box-sizing: border-box;
}

.footer-link-items h2 {
    margin-bottom: 16px;
}

.footer-link-items > h2 {
    color: #fff;
}

.footer-link-items a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 8px;
}

.footer-link-items a:hover {
    color: #e9e9e9;
    transition: 0.3s ease-out;
}

.footer-email-form h2 {
    margin-bottom: 32px;
}

.footer-input::placeholder {
    color: #b1b1b1;
}

.link-icon {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
    margin: 40px auto 0 auto;
    color: #fff;

}

.text {
    color: #fff;
    vertical-align: middle;
    margin-right: 10px;
    font-size: 12px;
    padding-top: 1px;
}

@media screen and (max-width: 820px) {
    .footer-links {
      padding-top: 2rem;
    }
  
    .footer-input {
      width: 100%;
    }
  
    .btn {
      width: 100%;
    }
  
    .footer-link-wrapper {
      flex-direction: column;
    }
  
    .link-icon {
      flex-direction: column;
    }
  }
  
