
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat','Times New Roman';
}

.about,
.contact,
.experience{
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}


.blog {
  background-image: url('cruisesea.jpg');
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
  height: 70vh;
}

.experience {
  background-image: url('components/IMG_2033.jpeg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
  max-height: 70vh;
}


@media screen and (max-width: 800px) {
  .experience {
    width: 100%;
    font-size: 100%;
    padding: 0;
  }
}